import axios from 'axios'

const state = {
  token: localStorage.getItem('token') || null,
  user: (() => {
    const userStr = localStorage.getItem('user');
    if (userStr && userStr !== 'undefined') {
      try {
        return JSON.parse(userStr);
      } catch (e) {
        console.error(':', e);
        return null;
      }
    }
    return null;
  })(),
  loading: false,
  error: null
}

const getters = {
  isAuthenticated: state => !!state.token,
  isAdmin: state => state.user && state.user.role === 'admin',
  currentUser: state => state.user,
  authError: state => state.error,
  authLoading: state => state.loading
}

const mutations = {
  AUTH_REQUEST(state) {
    state.loading = true
    state.error = null
  },
  
  AUTH_SUCCESS(state, { token, user }) {
    state.token = token
    state.user = user
    state.loading = false
    state.error = null
  },
  
  AUTH_ERROR(state, error) {
    state.loading = false
    state.error = error
  },
  
  LOGOUT(state) {
    state.token = null
    state.user = null
  },
  
  UPDATE_USER(state, user) {
    state.user = { ...state.user, ...user }
  }
}

const actions = {
  // 
  async login({ commit, dispatch }, credentials) {
    try {
      commit('AUTH_REQUEST')
      
      const loginPath = '/api/auth/login'
      // 
      console.log(':', {
        baseURL: axios.defaults.baseURL,
        email: credentials.email,
        loginPath: loginPath,
        fullURL: `${axios.defaults.baseURL}${loginPath}`
      });
      
      const response = await axios.post(loginPath, credentials)
      const { token, user } = response.data
      
      // 
      localStorage.setItem('token', token)
      localStorage.setItem('user', JSON.stringify(user))
      
      console.log(':', {
        tokenReceived: !!token,
        tokenPrefix: token ? token.substring(0, 10) + '...' : '',
        userReceived: !!user,
        localStorageSaved: {
          token: !!localStorage.getItem('token'),
          user: !!localStorage.getItem('user')
        }
      })
      
      // 
      commit('AUTH_SUCCESS', { token, user })
      
      // axiosAuthorization
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
      
      // 
      try {
        await dispatch('user/fetchCurrentUser', null, { root: true })
      } catch (error) {
        console.warn('')
      }
      
      // 
      dispatch('addNotification', {
        type: 'success',
        message: `${user.firstName}`,
        title: ''
      }, { root: true })
      
      return { success: true }
    } catch (error) {
      const message = error.response?.data?.error || ''
      commit('AUTH_ERROR', message)
      
      // 
      dispatch('addNotification', {
        type: 'error',
        message: message,
        title: ''
      }, { root: true }).catch(err => {
        console.error(':', err);
      });
      
      return { success: false, message }
    }
  },
  
  // token - 
  refreshTokenState({ commit }) {
    try {
      // localStoragetoken
      const token = localStorage.getItem('token')
      let user = null
      
      try {
        const userStr = localStorage.getItem('user')
        if (userStr) {
          user = JSON.parse(userStr)
        }
      } catch (e) {
        console.error(':', e)
      }
      
      if (token) {
        // store
        commit('AUTH_SUCCESS', { token, user })
        
        // axios
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
        
        console.log(':', {
          tokenRefreshed: true,
          userRefreshed: !!user
        })
        
        return true
      }
      
      return false
    } catch (error) {
      console.error(':', error)
      return false
    }
  },
  
  // authError
  authError({ commit, dispatch }, message) {
    commit('AUTH_ERROR', message);
    
    // 
    dispatch('addNotification', {
      type: 'error',
      message: message || '',
      title: ''
    }, { root: true }).catch(err => {
      console.error(':', err);
    });
    
    return { success: false, message };
  },
  
  // 
  async restoreSession({ commit, state }) {
    if (!state.token) return { success: false }
    
    try {
      // axiosAuthorization
      axios.defaults.headers.common['Authorization'] = `Bearer ${state.token}`
      
      // 
      // 
      let response;
      
      if (window.location.pathname === '/') {
        try {
          response = await axios.get('/api/users/me', {
            _silentError: true // 
          });
        } catch (e) {
          console.log('');
          // 
          commit('LOGOUT');
          localStorage.removeItem('token');
          localStorage.removeItem('user');
          delete axios.defaults.headers.common['Authorization'];
          return { success: false };
        }
      } else {
        response = await axios.get('/api/users/me');
      }
      
      console.log(':', response.data);
      
      // 
      if (response.data) {
        commit('AUTH_SUCCESS', { 
          token: state.token,
          user: response.data
        });
        
        // 
        localStorage.setItem('user', JSON.stringify(response.data));
      }
      
      return { success: true }
    } catch (error) {
      console.error(':', error)
      // 
      commit('LOGOUT')
      localStorage.removeItem('token')
      localStorage.removeItem('user')
      delete axios.defaults.headers.common['Authorization']
      return { success: false }
    }
  },
  
  // 
  logout({ commit }) {
    // 
    localStorage.removeItem('token')
    localStorage.removeItem('user')
    
    // axiosAuthorization
    delete axios.defaults.headers.common['Authorization']
    
    // 
    commit('LOGOUT')
  },
  
  // 
  updateUserInfo({ commit }, user) {
    // 
    localStorage.setItem('user', JSON.stringify({ ...state.user, ...user }))
    
    // 
    commit('UPDATE_USER', user)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
} 