import axios from 'axios'

const state = {
  users: [],
  loading: false,
  error: null,
  pagination: {
    total: 0,
    pages: 0,
    page: 1,
    perPage: 10
  }
}

const getters = {
  allUsers: state => state.users,
  isLoading: state => state.loading,
  error: state => state.error,
  pagination: state => state.pagination
}

const mutations = {
  SET_USERS(state, users) {
    state.users = users
  },
  
  SET_LOADING(state, loading) {
    state.loading = loading
  },
  
  SET_ERROR(state, error) {
    state.error = error
  },
  
  SET_PAGINATION(state, pagination) {
    state.pagination = pagination
  },
  
  ADD_USER(state, user) {
    state.users.push(user)
  },
  
  UPDATE_USER(state, updatedUser) {
    const index = state.users.findIndex(u => u.id === updatedUser.id)
    if (index !== -1) {
      state.users[index] = updatedUser
    }
  },
  
  REMOVE_USER(state, userId) {
    state.users = state.users.filter(u => u.id !== userId)
  }
}

const actions = {
  // 
  async fetchCurrentUser({ commit, dispatch }) {
    try {
      commit('SET_LOADING', true)
      commit('SET_ERROR', null)
      
      const response = await axios.get('/api/users/me')
      const userData = response.data
      
      // auth
      commit('auth/UPDATE_USER', userData, { root: true })
      
      // 
      localStorage.setItem('user', JSON.stringify(userData))
      
      return userData
    } catch (error) {
      console.error(':', error)
      const errorMessage = error.response?.data?.error || ''
      commit('SET_ERROR', errorMessage)
      
      if (error.response?.status === 401) {
        // tokentoken
        dispatch('auth/refreshTokenState', null, { root: true })
      }
      
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },
  
  // 
  async fetchUsers({ commit }, { page = 1, limit = 10, search = '', role = '', status = '' } = {}) {
    commit('SET_LOADING', true)
    commit('SET_ERROR', null)
    
    try {
      console.log(':', {
        baseURL: axios.defaults.baseURL,
        endpoint: '/api/users',
        params: { page, perPage: limit, search, role, status }
      })
      
      const response = await axios.get('/api/users', {
        params: {
          page,
          perPage: limit,
          search,
          role,
          status
        }
      })
      
      // 
      let users = []
      let totalItems = 0
      let totalPages = 0
      
      // 
      // 1. { users: [...], total: X, pages: Y }
      // 2. { data: [...], meta: { total: X, pages: Y } }
      
      if (response.data.users) {
        // 1
        users = response.data.users
        totalItems = response.data.total || 0
        totalPages = response.data.pages || 0
      } else if (response.data.data) {
        // 2
        users = response.data.data
        totalItems = response.data.meta?.total || 0
        totalPages = response.data.meta?.pages || 0
      } else {
        // 
        users = Array.isArray(response.data) ? response.data : []
        totalItems = users.length
      }
      
      const pagination = {
        total: totalItems,
        pages: totalPages,
        page: page,
        perPage: limit
      }
      
      commit('SET_USERS', users)
      commit('SET_PAGINATION', pagination)
      commit('SET_LOADING', false)
      
      return response.data
    } catch (error) {
      const message = error.response?.data?.error || ''
      commit('SET_ERROR', message)
      commit('SET_LOADING', false)
      throw error
    }
  },
  
  // 
  async createUser({ commit, dispatch }, userData) {
    try {
      commit('SET_LOADING', true)
      
      // 
      const requestData = {
        username: userData.username,
        email: userData.email,
        password: userData.password,
        first_name: userData.first_name,
        last_name: userData.last_name || ' ', // 
        department: userData.department || '',
        role: userData.role || 'user',
        status: userData.status || 'active'
      }
      
      console.log(':', {
        baseURL: axios.defaults.baseURL,
        endpoint: '/api/users',
        data: { ...requestData, password: '******' } // 
      })
      
      const response = await axios.post('/api/users', requestData)
      
      // 
      let user = null
      if (response.data.user) {
        user = response.data.user
      } else if (response.data.data) {
        user = response.data.data
      } else {
        // 
        user = response.data
      }
      
      if (user) {
        commit('ADD_USER', user)
      }
      
      commit('SET_LOADING', false)
      commit('SET_ERROR', null)
      
      // 
      dispatch('addNotification', {
        type: 'success',
        message: '',
        title: ''
      }, { root: true })
      
      return response.data
    } catch (error) {
      const message = error.response?.data?.error || ''
      commit('SET_ERROR', message)
      commit('SET_LOADING', false)
      
      // 
      dispatch('addNotification', {
        type: 'error',
        message: message,
        title: ''
      }, { root: true })
      
      throw error
    }
  },
  
  // 
  async updateUser({ commit, dispatch }, { id, data }) {
    try {
      commit('SET_LOADING', true)
      
      // 
      const requestData = {}
      
      // 
      if (data.username) requestData.username = data.username
      if (data.email) requestData.email = data.email
      if (data.first_name) requestData.first_name = data.first_name
      if (data.last_name) requestData.last_name = data.last_name
      if (data.department !== undefined) requestData.department = data.department
      if (data.role) requestData.role = data.role
      if (data.status) requestData.status = data.status
      if (data.profile_picture) requestData.profile_picture = data.profile_picture
      if (data.new_password) requestData.new_password = data.new_password
      
      const response = await axios.put(`/users/${id}`, requestData)
      
      // 
      let updatedUser = null
      if (response.data.user) {
        updatedUser = { ...response.data.user }
      } else if (response.data.data) {
        updatedUser = { ...response.data.data }
      } else {
        // 
        updatedUser = { id, ...data }
      }
      
      // auth
      const currentUser = JSON.parse(localStorage.getItem('user'))
      if (currentUser && currentUser.id === id) {
        dispatch('auth/updateUserInfo', data, { root: true })
      }
      
      commit('UPDATE_USER', updatedUser)
      commit('SET_LOADING', false)
      commit('SET_ERROR', null)
      
      // 
      dispatch('addNotification', {
        type: 'success',
        message: '',
        title: ''
      }, { root: true })
      
      return response.data
    } catch (error) {
      const message = error.response?.data?.error || ''
      commit('SET_ERROR', message)
      commit('SET_LOADING', false)
      
      // 
      dispatch('addNotification', {
        type: 'error',
        message: message,
        title: ''
      }, { root: true })
      
      throw error
    }
  },
  
  // 
  async deleteUser({ commit, dispatch }, id) {
    try {
      commit('SET_LOADING', true)
      
      await axios.delete(`/users/${id}`)
      
      commit('REMOVE_USER', id)
      commit('SET_LOADING', false)
      commit('SET_ERROR', null)
      
      // 
      dispatch('addNotification', {
        type: 'success',
        message: '',
        title: ''
      }, { root: true })
      
      return { success: true }
    } catch (error) {
      const message = error.response?.data?.error || ''
      commit('SET_ERROR', message)
      commit('SET_LOADING', false)
      
      // 
      dispatch('addNotification', {
        type: 'error',
        message: message,
        title: ''
      }, { root: true })
      
      throw error
    }
  },
  
  // 
  async updateProfile({ commit, dispatch }, profileData) {
    try {
      commit('SET_LOADING', true)
      
      const response = await axios.put('/api/users/me', profileData)
      
      // 
      dispatch('auth/updateUserInfo', profileData, { root: true })
      
      commit('SET_LOADING', false)
      commit('SET_ERROR', null)
      
      // 
      dispatch('addNotification', {
        type: 'success',
        message: '',
        title: ''
      }, { root: true })
      
      return response.data
    } catch (error) {
      const message = error.response?.data?.error || ''
      commit('SET_ERROR', message)
      commit('SET_LOADING', false)
      
      // 
      dispatch('addNotification', {
        type: 'error',
        message: message,
        title: ''
      }, { root: true })
      
      throw error
    }
  },
  
  // 
  async searchUsers({ commit, dispatch }, query) {
    try {
      commit('SET_LOADING', true)
      
      // fetchUsersAPI
      return dispatch('fetchUsers', { 
        search: query, 
        page: 1, 
        limit: 10 
      })
    } catch (error) {
      commit('SET_ERROR', error.message || '')
      return []
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
} 