// asset.js - 
import { ElMessage } from 'element-plus'

export default {
  namespaced: true,
  
  state: {
    assets: [],
    asset: null,
    loading: false,
    error: null,
    pagination: {
      total: 0,
      page: 1,
      perPage: 10
    }
  },
  
  getters: {
    assets: state => state.assets,
    asset: state => state.asset,
    isLoading: state => state.loading,
    error: state => state.error,
    pagination: state => state.pagination
  },
  
  mutations: {
    SET_ASSETS(state, assets) {
      state.assets = assets
    },
    
    SET_ASSET(state, asset) {
      state.asset = asset
    },
    
    ADD_ASSET(state, asset) {
      state.assets.unshift(asset)
    },
    
    UPDATE_ASSET(state, updatedAsset) {
      const index = state.assets.findIndex(a => a.id === updatedAsset.id)
      if (index !== -1) {
        state.assets.splice(index, 1, updatedAsset)
      }
      if (state.asset && state.asset.id === updatedAsset.id) {
        state.asset = updatedAsset
      }
    },
    
    REMOVE_ASSET(state, id) {
      state.assets = state.assets.filter(a => a.id !== id)
      if (state.asset && state.asset.id === id) {
        state.asset = null
      }
    },
    
    SET_LOADING(state, loading) {
      state.loading = loading
    },
    
    SET_ERROR(state, error) {
      state.error = error
    },
    
    SET_PAGINATION(state, pagination) {
      state.pagination = { ...state.pagination, ...pagination }
    }
  },
  
  actions: {
    // 
    async fetchAssets({ commit }, { page = 1, perPage = 10, search = '', filters = {} } = {}) {
      commit('SET_LOADING', true)
      
      try {
        // API
        console.log('Fetching assets with params:', { page, perPage, search, filters })
        
        // 
        setTimeout(() => {
          const assets = []
          const total = 0
          
          commit('SET_ASSETS', assets)
          commit('SET_PAGINATION', { total, page, perPage })
          commit('SET_LOADING', false)
        }, 500)
        
        // API
        // const response = await fetch(`/api/assets?page=${page}&perPage=${perPage}&search=${search}&...`)
        // const data = await response.json()
        // commit('SET_ASSETS', data.items)
        // commit('SET_PAGINATION', { 
        //   total: data.total,
        //   page: data.page,
        //   perPage: data.perPage
        // })
      } catch (error) {
        console.error(':', error)
        commit('SET_ERROR', '')
        ElMessage.error('')
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    // 
    async fetchAssetById({ commit }, id) {
      commit('SET_LOADING', true)
      
      try {
        // API
        console.log('Fetching asset with id:', id)
        
        // 
        setTimeout(() => {
          const asset = {
            id,
            name: ` ${id}`,
            ip: '192.168.1.1',
            type: 'server',
            os: 'Linux',
            owner: '',
            status: 'active',
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString()
          }
          
          commit('SET_ASSET', asset)
          commit('SET_LOADING', false)
        }, 500)
        
        // API
        // const response = await fetch(`/api/assets/${id}`)
        // const data = await response.json()
        // commit('SET_ASSET', data)
      } catch (error) {
        console.error(':', error)
        commit('SET_ERROR', '')
        ElMessage.error('')
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    // 
    async createAsset({ commit }, assetData) {
      commit('SET_LOADING', true)
      
      try {
        // API
        console.log('Creating asset with data:', assetData)
        
        // 
        setTimeout(() => {
          const newAsset = {
            id: Date.now().toString(),
            ...assetData,
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString()
          }
          
          commit('ADD_ASSET', newAsset)
          commit('SET_LOADING', false)
          ElMessage.success('')
          return newAsset
        }, 500)
        
        // API
        // const response = await fetch('/api/assets', {
        //   method: 'POST',
        //   headers: {
        //     'Content-Type': 'application/json'
        //   },
        //   body: JSON.stringify(assetData)
        // })
        // const data = await response.json()
        // commit('ADD_ASSET', data)
        // ElMessage.success('')
        // return data
      } catch (error) {
        console.error(':', error)
        commit('SET_ERROR', '')
        ElMessage.error('')
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    // 
    async updateAsset({ commit }, { id, data }) {
      commit('SET_LOADING', true)
      
      try {
        // API
        console.log('Updating asset:', { id, data })
        
        // 
        setTimeout(() => {
          const updatedAsset = {
            id,
            ...data,
            updatedAt: new Date().toISOString()
          }
          
          commit('UPDATE_ASSET', updatedAsset)
          commit('SET_LOADING', false)
          ElMessage.success('')
          return updatedAsset
        }, 500)
        
        // API
        // const response = await fetch(`/api/assets/${id}`, {
        //   method: 'PUT',
        //   headers: {
        //     'Content-Type': 'application/json'
        //   },
        //   body: JSON.stringify(data)
        // })
        // const updatedAsset = await response.json()
        // commit('UPDATE_ASSET', updatedAsset)
        // ElMessage.success('')
        // return updatedAsset
      } catch (error) {
        console.error(':', error)
        commit('SET_ERROR', '')
        ElMessage.error('')
      } finally {
        commit('SET_LOADING', false)
      }
    },
    
    // 
    async deleteAsset({ commit }, id) {
      commit('SET_LOADING', true)
      
      try {
        // API
        console.log('Deleting asset with id:', id)
        
        // 
        setTimeout(() => {
          commit('REMOVE_ASSET', id)
          commit('SET_LOADING', false)
          ElMessage.success('')
        }, 500)
        
        // API
        // await fetch(`/api/assets/${id}`, {
        //   method: 'DELETE'
        // })
        // commit('REMOVE_ASSET', id)
        // ElMessage.success('')
      } catch (error) {
        console.error(':', error)
        commit('SET_ERROR', '')
        ElMessage.error('')
      } finally {
        commit('SET_LOADING', false)
      }
    }
  }
} 