import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import axios from 'axios'
import moment from 'moment'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'

// axios
// URL
if (process.env.NODE_ENV === 'development') {
  // URL/api
  axios.defaults.baseURL = process.env.VUE_APP_API_URL || 'http://localhost:8000'
  console.log('baseURL:', axios.defaults.baseURL)
} else {
  // 
  axios.defaults.baseURL = ''
  console.log('baseURL')
}

// axios
console.log(':', process.env.NODE_ENV)
console.log('AxiosURL:', axios.defaults.baseURL)

axios.interceptors.request.use(config => {
  const token = store.state.auth.token
  if (token) {
    config.headers.Authorization = `Bearer ${token}`
    console.log(`: ${config.url}`, {
      tokenExists: !!token,
      tokenPrefix: token ? token.substring(0, 10) + '...' : '',
      headers: config.headers
    })
  } else {
    console.warn(`: ${config.url}`, {
      storeAuthState: store.state.auth,
      localStorageToken: localStorage.getItem('token') ? '' : ''
    })
    
    // localStoragetoken
    const backupToken = localStorage.getItem('token')
    if (backupToken) {
      config.headers.Authorization = `Bearer ${backupToken}`
      console.log('localStorage')
    }
  }
  return config
})

// 
axios.interceptors.response.use(
  response => response,
  error => {
    // 
    console.error('Axios:', {
      url: error.config?.url,
      method: error.config?.method,
      baseURL: axios.defaults.baseURL,
      fullURL: axios.defaults.baseURL + (error.config?.url || ''),
      status: error.response?.status,
      statusText: error.response?.statusText,
      data: error.response?.data,
      headers: error.config?.headers,
      message: error.message
    });
    
    // 
    if (error.config && error.config._silentError) {
      console.log('')
      return Promise.reject(error);
    }
    
    // 
    const currentPath = window.location.pathname;
    console.log(':', currentPath);
    
    // 401
    if (error.response && 
        error.response.status === 401 && 
        currentPath !== '/') {
      console.log('401')
      store.dispatch('auth/logout')
      router.push('/login')
    }
    return Promise.reject(error)
  }
)

// localStorage
const userStr = localStorage.getItem('user');
if (userStr === 'undefined' || (userStr && userStr.trim() === '')) {
  console.warn('...');
  localStorage.removeItem('user');
  localStorage.removeItem('token');
}

// 
const app = createApp(App)

// Element Plus
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

// 
app.config.globalProperties.$moment = moment
app.config.globalProperties.$axios = axios

// 
app.config.globalProperties.$filters = {
  formatDate(value) {
    if (!value) return ''
    return moment(value).format('YYYY-MM-DD HH:mm')
  },
  formatDateOnly(value) {
    if (!value) return ''
    return moment(value).format('YYYY-MM-DD')
  },
  capitalize(value) {
    if (!value) return ''
    value = value.toString()
    return value.charAt(0).toUpperCase() + value.slice(1)
  },
  severityClass(severity) {
    switch (severity) {
      case 'critical': return 'danger'
      case 'high': return 'danger'
      case 'medium': return 'warning'
      case 'low': return 'primary'
      case 'info': return 'info'
      default: return 'info'
    }
  },
  statusClass(status) {
    switch (status) {
      case 'open': return 'danger'
      case 'in_progress': return 'warning'
      case 'resolved': return 'success'
      case 'closed': return 'info'
      case 'false_positive': return 'info'
      default: return 'info'
    }
  },
  statusText(status) {
    switch (status) {
      case 'open': return ''
      case 'in_progress': return ''
      case 'resolved': return ''
      case 'closed': return ''
      case 'false_positive': return ''
      default: return status
    }
  },
  severityText(severity) {
    switch (severity) {
      case 'critical': return ''
      case 'high': return ''
      case 'medium': return ''
      case 'low': return ''
      case 'info': return ''
      default: return severity
    }
  }
}

// 
app.use(store)
app.use(router)
app.use(ElementPlus)

// 
app.mount('#app') 